<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Create Category</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/maincategories">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <template>
      <v-form @submit.prevent="formSubmit" ref="form" v-model="valid" lazy-validation>
        <v-text-field
            v-model="formData.cat_name"
            :rules="catRules"
            label="Category name"></v-text-field>
          <v-file-input
              v-model="formData.selectedFile"
              :rules="filesRules"
              label="Category Image"
              accept="image/*"
              @change="handleFileChange"
          ></v-file-input>
          <v-img v-if="previewImage" :src="previewImage" class="img-fluid" width="100" height="100"></v-img>
          <!-- <v-select
              v-model="formData.cat_feature"
              :items="features"
              item-text="text"
              item-value="id"
              label="Category Feature"></v-select> -->
          <!-- <v-text-field 
              type="number" 
              v-model="formData.cat_sort" 
              label="Category Sort"></v-text-field> -->
          
          <!-- <v-btn color="error" class="mr-4" @click="reset">CANCEL</v-btn>
          <v-btn v-if="isLoading" class="mr-4 cred">Loading...</v-btn>
          <v-btn v-else type="submit" :disabled="!valid" color="success" class="mr-4">SAVE</v-btn> -->
          <v-col cols="12" sm="6" md="12" class="py-0 d-flex" style="justify-content: space-between;">
              <div></div>
              <div class="ms-3 pb-3">
                <v-btn class="my-4 me-3 cred" @click="reset">CANCEL</v-btn>
                <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                <v-btn v-else type="submit" :disabled="!valid" class="my-4 me-3 cred">SAVE</v-btn>
              </div>
          </v-col>
      </v-form>
    </template>

    <v-snackbar v-model="snackbar" :timeout="snackbar.timeout" class="snackbar-shadow">{{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
      </template>
    </v-snackbar>

  </div>
</template>

<script>
import axios from 'axios';
export default {
    name: "CreateCategory",
    components: {  },
    data(){
        return{
            valid: false,
            previewImage: null,
            isLoading: false,
            snackbar: false,
            text: '',
            timeout: '',
            features: [
              {'id':'0', 'text':"Normal"}, 
              {'id':'1', 'text':"Feature"},
              {'id':'2', 'text':"Top add"},
            ],
            formData: {
              cat_name: '',
              selectedFile: ''  ,
              cat_feature: '0',
              cat_sort: '1'
            },
            catRules: [
                v => !!v || 'Category name is required.',
                v => v.length <= 50 || 'Name must be less than 50 characters.',
            ],
            filesRules: [
                v => !!v || 'Category image is required.',
            ],
        }
    },
    mounted() {},
    methods: {
        reset(){
          this.previewImage = null;
          this.$refs.form.reset();
        },
        handleFileChange() {
          this.previewImage = URL.createObjectURL(this.formData.selectedFile);
        },
        formSubmit() {
            const config = {
                headers: { 'content-type': 'multipart/form-data' }
            }
            let data = { 
                cat_name: this.formData.cat_name,
                selectedFile: this.formData.selectedFile,
                cat_feature: this.formData.cat_feature,
                cat_sort: this.formData.cat_sort,
            };
            if(this.formData.cat_name!='' && this.formData.selectedFile!=''){
              this.isLoading = true;
              axios.post("category/add", data, config)
                .then((resp) => {
                  this.isLoading = false;
                  this.snackbar = true;
                  this.text = resp.data.message;
                  this.timeout = 3000;
                  this.previewImage = null;
                  this.$refs.form.reset();
                }
              );
            }
            this.$refs.form.validate();
        },
    },
}
</script>